import React, { Component, Fragment } from 'react';
// import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { Table, Popconfirm } from 'antd';
import { ListClassOfficers } from 'dump/data';


const rowSelection = {
  onChange: (selectedRowKeys, selectedRows) => {
    console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
  },
  getCheckboxProps: record => ({
    disabled: record.name === 'Disabled User', // Column configuration not to be checked
    name: record.name,
  }),
};


const columns = [

  {
    title: 'Mã SV',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Họ tên',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'birthdate',
    key: 'birthdate',
  },
  {
    title: 'Chức danh',
    dataIndex: 'CD',
    key: 'CD',
  },
  {
    title: 'Chức danh kim',
    dataIndex: 'CDK',
    key: 'CDK',
  },
  {
    title: 'Thao tác',
    dataIndex: 'operation',
    render: (text, record) =>
      ListClassOfficers.length >= 1 ? (
        <Fragment>
          {/* <BtnEdit href={`#ListClassOfficers/form/edit/${record.codeRating}`} /> */}
          <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.key)}>
            <BtnDelete />
          </Popconfirm>
        </Fragment>
      ) : null,
  },
];

class TableListClassOfficers extends Component {
  state = {};

  render() {

    return (
      <Table rowSelection={rowSelection} bordered dataSource={ListClassOfficers} columns={columns} />
    );
  }
}

export default TableListClassOfficers;



