import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import { Card, Row, Col } from 'antd';
import TableListClassOfficers from './TableListClassOfficers';
import SearchBar from './SearchBar';
import './style.scss'


class ListClassOfficers extends Component {
  state = {};

  render() {

    return (
      <Card title="Danh sách cán bộ lớp">
        <SearchBar />
        <Row>
          <Col span={24}>
            <BtnAddNew title="Thêm" href="#/listClassOfficers/from" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableListClassOfficers />
          </Col>
        </Row>
      </Card >
    );
  }
}

export default props => (
  <Page {...props}>
    <ListClassOfficers />
  </Page>
);



